<template>
  <div class="main">
    <div class="cont">
      <div class="close" @click="close">
        <i class="fa fa-times-circle" aria-hidden="true"></i>
      </div>
      <div class="cont-main">
        <div class="img">
          <img :src="product.imgurl || '/static/img/hongyanbg.jpg'" alt="">
        </div>
        <div class="info">
          <div class="price">{{price ? price + '万' : '0'}}</div>
        </div>
      </div>
      <div class="options">
        <detail-rank
          v-for="(item, index) in details"
          :key="item.config_id"
          :title="item.config_name"
          :options="item.config_options"
        ></detail-rank>
      </div>
    </div>
  </div>
</template>

<script>
import bus from '../../../assets/js/bus'
import DetailRank from './detail-rank.vue'
export default {
  name: 'Mall-Detail-Popup',
  props: {
    product: '',
    price: '',
    details: ''
  },
  methods: {
    close () {
      bus.$emit('closePopup', 2)
    },
    selectOption (obj, index) {
      bus.$emit('clickOption', obj, index)
    }
  },
  components: {
    DetailRank
  }
}
</script>

<style lang="less" scoped>
  .main{
    .cont{
      position: relative;
      transition: transform .3s;
      transform: translate3d(0,0,0);
      .close{
        position: absolute;
        top: .2rem;
        right: .2rem;
        font-size: 1.5rem;
        text-align: right;
        vertical-align: top;
        width: 1.5rem;
        height: 1.5rem;
        z-index: 10;
        color: #ddd;
      }
      .cont-main{
        display: flex;
        .img{
          width: 7rem;
          height: 6rem;
          margin-top: -10%;
          margin-left: 1rem;
          img {
            width: 100%;
            height:100%;
          }
        }
        .info{
          position: relative;
          flex: 1;
          margin-left: 1.16rem;
          margin-top: .6rem;
          .price{
            position: relative;
            display: inline-block;
            font-size: 1.5rem;
            color: #f56600;
            &:before{
              content: '￥';
              font-size: 14px;
            }
          }
          .desc{
            font-size: 13px;
            color: rgba(0,0,0,.87);
          }
        }
      }
      .options{
        max-height: 26rem;
        overflow-y: auto;
        overflow-x: hidden;
        margin-top: 1.6rem;
        border-top: 1px solid #ddd;
        .line-item{
          .item-title{
            margin-top: .6rem;
            margin-bottom: .4rem;
            line-height: 1.24rem;
            font-size: 13px;
            color: rgba(0,0,0,.87);
          }
          .item-cont{
            display: flex;
            flex-direction: row;
            padding-left: .6rem;
            align-items: center;
            padding-bottom: 1.32rem;
            flex-wrap: wrap!important;
            border-bottom: 1px solid #f4f4f4;
            .o-i{
              box-sizing: border-box;
              border: 1px solid rgba(0,0,0,.15);
              border-radius: 2px;
              margin-right: .6rem;
              &:last-child{
                margin-right: 0;
              }
              p{
                margin: 0;
                padding: .5rem 1rem;
                font-size: .9rem;
              }
            }
          }
        }
      }
    }
  }
</style>



