<template>
  <div class="shop-wrap">
    <p>
    <p data-v-c28106ec="" class="shop">
      该产品由
      <router-link :to="'/shop/' + unitInfo.unit_id">{{unitInfo.unit_name}}</router-link>
      负责发货, 并提供售后服务
    </p>
  </div>
</template>

<script>
  export default {
    props: {
      unitInfo: Object
    }
  }
</script>

<style lang="less" scoped>
  .shop-wrap{
    p{
      margin: 0;
      display: block;
      font-size: 14px;
      color: #999;
      line-height: 32px;
      a {
        font-size: 14px;
        color: #e26204;
      }
    }
  }

</style>



