<template>
  <div class="ios-s detail-wrap">
    <div class="swipe-wrap">
      <div class="header-btns">
        <div class="btn-item" @click="collect">
          <i v-show="collectState" class="fa fa-star" aria-hidden="true" style="color: orange;"></i>
          <i v-show="!collectState" class="fa fa-star-o" aria-hidden="true"></i>
        </div>
        <router-link to="/mall/cart" class="btn-item">
          <i class="fa fa-shopping-cart" aria-hidden="true"></i>
        </router-link>
      </div>
      <mt-swipe :auto="0">
        <mt-swipe-item
          v-for="(item, index) in detail.imgurls"
          :key="index"
        >
          <img :src="item || '/static/img/default-bg.jpg'" alt="">
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <div class="main">
      <div class="title-view">
        <div class="name">{{detail.name}}</div>
        <Shop
          v-if="detail.unit_id"
          :unit-info="{unit_id: detail.unit_id, unit_name: detail.unit_name}" />
        <div class="desc">
          {{detail.labels_name}}
        </div>
        <div class="desc">￥{{detail.zc?detail.price_min + '万':detail.price_min}} ~ ￥{{detail.zc?detail.price_max + '万':detail.price_max}}</div>
        <div class="desc" v-show="detail.labels_name === '配件'">{{'图号:' + detail.spare_parts_code}}</div>
        <div class="desc">库存: {{detail.storage}}</div>
        <div class="money">
           <div class="price" v-if="detail.zc">{{initPrice(price) || initPrice(detail.price_min)}}</div>
          <div class="price" v-else>{{price || detail.price_min}}</div>
        </div>
        <div style="    font-size: 13px;
    color: seagreen;
    padding: .5rem 0;" v-if="detail.integral">消耗积分:{{detail.integral}}</div>
      </div>
      <div class="select-option" @click="selectOption" v-show="detail.detailCount > 1 && !detail.coupon">
        <div class="cont">
          <div class="w-2">已选</div>
          <div class="f-1">
            <div class="product-info">
              {{option || '请选择配置'}}
            </div>
            <i class="fa fa-chevron-right icon" aria-hidden="true"></i>
          </div>
        </div>
      </div>
      <div class="info">
        <div v-show="selectedOptions">
          <div class="title">已选配置</div>
          <div style="padding: 1rem;">
            <span v-for="(item, index) in selectedOptions" class="text-nowrap" style="display: inline-block; width: 50%; border-bottom: 1px solid #999;">{{index}}： {{item}}</span>
          </div>
        </div>
        <div v-if="detail.description">
          <div class="title">使用规则</div>
          <div class="detail" v-html="detail.description"></div>
        </div>
        <div class="title">商品详情</div>
        <div class="detail" id="goods-detail" v-html="detail.article"></div>
      </div>
    </div>
    <mt-popup
      v-model="popupVisible"
      style="height: 70%; width: 100%; z-index: 99999;"
      position="bottom">
        <detail-popup
          :product="detail"
          :price="price"
          :details="details"
        ></detail-popup>
    </mt-popup>
  </div>
</template>

<script>
  /* eslint-disable */
import {collectProduct} from '../../../api/mall'
import Shop from './shop'
import DetailPopup from './detail-popup.vue'
import bus from '../../../assets/js/bus'
import * as types from '../../../store/mutation-types'
import {getProductDetail, addProductToCart, getOptions, mallOrderGetPrice} from '../../../api/mall'
import {pushIntention} from '../../../api/maintain'
import {Menu, setLocalStorage } from '../../../lib/util'
export default {
  name: 'Mall-Detail',
  data () {
    return {
      detail: '',
      details: null,
      option: '',
      price: '',
      detailID: '',
      cartNum: 0,
      popupVisible: false,
      // 收藏状态
      collectState: false,
      // 选择后的配置
      selectedOptions: null
    }
  },
  created () {
    this.getDatas()
  },
  beforeDestroy () {
    bus.$off('closePopup')
    bus.$off('addCart')
    bus.$off('clickSelectedOptions')
    bus.$off('buyNow')
    bus.$off('addCardError')
    bus.$off('buyNowError')
  },
    mounted () {
    bus.$on('closePopup', (v) => {
      this.$set(this, 'popupVisible', false)
    })
//    bus.$on('clickOption', (obj, index) => {
//      this.$set(this, 'popupVisible', false)
//      this.$set(this, 'price', obj.price)
//      this.$set(this, 'detailID', obj.detail_id)
//      this.$set(this, 'option', '配置' + (index + 1))
//      this.$store.commit(types.MODIFY_ADD_BTN_STATE, false)
//    })
    bus.$on('addCart', () => {
      if ((!this.detail.sale && !this.detail.unit_id) || !this.detail.storage) return this.$toast('暂不支持购买')
      pushIntention(this.detail.mall_id, 2, 2)
      addProductToCart(this.detailID)
        .then(data => {
          if (data.state == 301) {
            setTimeout(() => {
              return this.$router.push('/login')
            }, 1000)
          }
          this.$toast(data.errormsg)
        })
    })
    // 选择配置
    bus.$on('clickSelectedOptions', () => {
      let selectedOptions = this.$store.getters.allSelectedOptions
      let _selected = []
      let _selectedStr = ''
      this.details = Menu.check(this.$store.getters.allSelectedOptions)
      for (let i of selectedOptions) {
        _selected.push(i.name)
      }
      _selected.forEach((item, index) => {
        _selectedStr ? _selectedStr += `,"${item}"` : _selectedStr = `"${item}"`
      })
      getOptions(this.$route.params.id, `[${_selectedStr}]`)
        .then(data => {
          if (data.state === 4) {
            this.price = data.data.info.price
            this.detailID = data.data.info.detail_id
            this.selectedOptions = data.data.table
            if (data.data.sale || data.data.unit_id) {
              this.$store.commit(types.MODIFY_ADD_BTN_STATE, false)
            }
            if (!data.data.storage) {
                this.$store.commit(types.MODIFY_ADD_BTN_STATE, true)
            }
          } else if (!data.state || data.state === 5) {
            this.price = 0
          }
        })
    })
    // 立即购买处理
    bus.$on('buyNow', () => {
      if (!this.detailID) {
        return this.$toast('请选择配置')
      }
      pushIntention(this.detail.mall_id, 2, 2)
      let datas = {}
      datas[this.detailID] = 1
      mallOrderGetPrice(JSON.stringify(datas))
        .then(data => {
          if (data.state) {
            if (data.state == 301) {
              return this.$router.push('/login')
            }
            setLocalStorage('orderInfo', JSON.stringify(data.data))
            setLocalStorage('detail', JSON.stringify(datas))
            this.$router.push('/mall/confirm')
          } else {
            this.$toast(data.errormsg || '网络错误，请重试')
          }
        })
        .catch(err => console.log(err))
    })
    // 无法添加到购物车处理
    bus.$on('addCardError', () => {
      if ((!this.detail.sale && !this.detail.unit_id) || !this.detail.storage) {
        this.$toast('暂时无法购买')
      } else {
        this.$toast('请先选择配置')
      }
    })
    // 无法立即购买处理
    bus.$on('buyNowError', () => {
      if ((!this.detail.sale && !this.detail.unit_id) || !this.detail.storage) {
        this.$toast('暂时无法购买')
      } else {
        this.$toast('请先选择配置')
      }
    })
  },
  methods: {
    getDatas () {
      getProductDetail(this.$route.params.id)
        .then(data => {
          if (data.state) {
            this.$set(this, 'detail', data.data.info)
            if (data.data.info.collect) {
              this.collectState = true
            }
            // 如果当前商品只有一个配置
            if (this.detail.detail.length === 1) {
              this.detailID = this.detail.detail[0].detail_id
              this.$store.commit(types.MODIFY_ADD_BTN_STATE, false)
            }
            // 如果当前商品未开放购买，将加入购物车按钮变为不可选状态
            if (!this.detail.sale && !this.detail.unit_id) {
                this.$store.commit(types.MODIFY_ADD_BTN_STATE, true)
            }
            // 如果当前商品库存不足，将加入购物车按钮变为不可选状态
            if (!this.detail.storage) {
                this.$store.commit(types.MODIFY_ADD_BTN_STATE, true)
            }
            if (data.data.info.detail.length > 1) {
              this.details = Menu.init(data.data.info.detail).detailOption
            }
          } else {
            this.$toast(data.errormsg || '网络错误')
          }
        })
    },
    selectOption () {
      this.$set(this, 'popupVisible', true)
    },
    initPrice (price) {
      if (!price) return 0
      let _price = price + '万'
      return _price
    },
    // 收藏商品
    collect () {
      pushIntention(this.detail.mall_id, 1, 2)
      collectProduct(this.detail.mall_id)
        .then(data => {
          if (data.state) {
            this.$toast(data.errormsg)
            this.collectState = !this.collectState
            this.getDatas()
          } else {
            this.$toast(data.errormsg)
          }
        })
        .catch(err => {
          console.error(err)
          this.$toast('网络繁忙')
        })
    }
  },
  components: {
    DetailPopup,
    Shop
  }
}
</script>

<style lang="less" scoped>
  .detail-wrap{
    position: relative;
    padding-bottom: 60px;
    .swipe-wrap{
      position: relative;
      width: 100%;
      height: 15rem;
      min-height: 240px;
      max-height: 240px;
      .header-btns{
        top: 1rem;
        left: 0;
        right: 0;
        position: fixed;
        z-index: 9;
        .btn-item{
          float: right;
          display: inline-block;
          padding: .5rem;
          margin-right: .5rem;
          border-radius: 50%;
          color: #fff;
          background: rgba(0, 0,0, .3);
        }
        i {
          font-size: 1.5rem;
        }
      }
      img{
        width: 100%;
        height: 100%;
      }
    }
    .main{
      background: #fff;
      .title-view{
        padding: .6rem .6rem 0;
        text-align: left;
        .name{
          font-size: 1.36rem;
          color: rgba(0,0,0,.87);
        }
        .desc{
          margin-top: .08rem;
          font-size: 13px;
          color: rgba(0,0,0,.54);
        }
        .money{
          padding: .16rem 0;
          color: maroon;
          border-bottom: 1px solid #f4f4f4;
          .price{
            font-size: 18px;
            &:before{
              content: "支付价格:￥";
              font-size: 13px;
              color: maroon;
            }
          }
        }
      }
      .select-option{
        padding: .6rem;
        text-align: left;
        border-bottom: 1px solid #f4f4f4;
        position: relative;
        .cont{
          display: flex;
          font-size: 13px;
          position: relative;
          .w-2{
            width: 2rem;
            color: rgba(0,0,0,.54);
          }
          .f-1{
            flex: 1;
            color: rgba(0,0,0,.87);
          }
          .icon{
            position: absolute;
            right: 0;
            top: 25%;
            color: rgba(0,0,0,.54);
          }
        }
      }
      .info{
        .title{
          padding: 1rem;
          background-color: #eee;
        }
        .detail{
          padding: 1rem;
        }
      }
    }
  }
</style>



