<template>
  <div class="mall-rank">
    <div class="title">{{title}}</div>
    <div>
      <mall-option
        v-for="(item, index) in options"
        :key="item.detail_id"
        v-bind:option="item"
        :ref="item.option_name_cn"
      ></mall-option>
    </div>
  </div>
</template>

<script>
import MallOption from './detail-rank-option.vue'

export default {
  name: 'Mall-Rank',
  props: {
    title: '',
    options: {type: Array}
  },
  data () {
    return {}
  },
  mounted () {
  },
  methods: {},
  components: {
    MallOption
  }
}
</script>

<style lang="less" scoped>
  .mall-rank{
    .title{
      font-size: 16px;
      margin: 2px 0 5px 5px;
      clear: both;
    }
    &:last-child {
      margin-bottom: 10rem;
    }
  }
</style>



