<template>
  <!--<span >{{option.option_name_cn}}</span>-->
  <mt-button
    v-on:click="selectedOption"
    :class="{'option-active': active}"
    :disabled="!option.isActive"
    class="mall-option" plain>{{option.option_name_cn}}</mt-button>
</template>

<script>
  /* eslint-disable */
import bus from '../../../assets/js/bus'
// import * as types from '../../../store/mutation-types'
export default {
  name: 'Mall-Option',
  props: {
    option: {type: Object}
  },
  data () {
    return {
      active: false
    }
  },
  beforeDestory () {
    bus.$off('clickSelectedOptions')
  },
  methods: {
    selectedOption () {
      this.$set(this, 'active', !this.active)
      if (this.active) {
        this.$store.commit('ADDSELECTEDOPTIONS', this.option)
        bus.$emit('clickSelectedOptions')
      } else {
        let _a = this.$store.getters.allSelectedOptions
        let obj = {}
        obj['id'] = this.option.config_id
        obj['name'] = this.option.option_name_cn
        let _b = _a.indexOf(obj)
        this.$store.commit('REMOVESELECTEDOPTIONS', _b)
        bus.$emit('clickSelectedOptions')
      }
    }
  }
}
</script>

<style lang="less" scoped>
  .mall-option{
    width: 152px;
    margin: 1%;
    padding: 5px 0;
    display: inline-block;
    text-align: center;
    border: 1px solid #aaa;
    cursor: pointer;
    &:hover{
      border: 1px solid darksalmon;
    }
  }
  .option-active {
    border-color: maroon;
    color: maroon;
  }
</style>



